.profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.profile__form {
  display: flex;
  flex-direction: column;
}

.profile__form__modify {
  padding: 10px;
  margin-bottom: 8px;
}

.profile__form__modify,
.profile__form__submit,
.profile__log-out {
  text-align: center;
  border-radius: 20px;
  width: 300px;
}

.profile__form__submit {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--twitter-color);
  padding: 10px;
  margin-bottom: 40px;
}

.profile__hr {
  padding: 0px 200px;
  border: 1px dotted white;
  margin-bottom: 50px;
}

.profile__log-out {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: tomato;
  padding: 10px;
  margin-bottom: 40px;
}
