.home-screen__tweet-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.home-screen__tweet-list__text {
  background-color: whitesmoke;
  padding: 20px 150px;
  width: 20vw;
  border-radius: 10px;
  color: grey;
  font-size: 17px;
  margin-bottom: 35px;
}

.home-screen__tweet-list__contents {
  display: flex;
  position: relative;
}

.home-screen__tweet-list__contents__img {
  padding: 0px;
  border-radius: 50%;
  position: absolute;
  right: 0px;
  bottom: 10px;
  width: 50px;
  height: 50px;
  background-color: black;
}

.home-screen__tweet-list__contents__tools {
  color: grey;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 67px;
  right: 5px;
}

.home-screen__tweet-list__contents__tools #modify {
  margin-left: 5px;
}

.home-screen__modify {
  background-color: white;
  border-radius: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
  width: 527px;
  height: 200px;
}

.home-screen__modify #modify-tweet,
.home-screen__modify #modify-submit,
.home-screen__modify #modify-cancel {
  width: 90%;
  border-radius: 20px;
  height: 50px;
}

.home-screen__modify #modify-submit,
.home-screen__modify #modify-cancel {
  margin: 5px;
  color: white;
}

.home-screen__modify #modify-tweet {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
  border: 2px solid black;
  text-align: center;
}

.home-screen__modify #modify-submit {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--twitter-color);
}
.home-screen__modify #modify-cancel {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: tomato;
}
