.nav {
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-list .nav-list__profile {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.nav-list i {
  color: var(--twitter-color);
  margin: 50px;
  font-size: 50px;
}

.nav-list:last-child {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1px;
}

.nav-list:last-child i {
  margin: 5px;
  margin-top: 10px;
}

.nav-list:last-child span {
  color: white;
  font-size: 14px;
}
