.home-screen__form__first-row {
  display: flex;
  position: relative;
  margin-bottom: 35px;
}

.home-screen__form__first-row__tweet {
  background-color: black;
  border: 1px solid var(--twitter-color);
  border-radius: 50px;
  font-size: 20px;
  padding: 10px 100px;
  padding-left: 10px;
  color: white;
}

.home-screen__form__first-row__submit {
  display: block;
  background-color: var(--twitter-color);
  font-size: 20px;
  color: white;
  border: none;
  border-radius: 50%;
  padding: 11px;
  margin: 0px;
  position: absolute;
  right: 0px;
}

.home-screen__form__second-row span {
  color: var(--twitter-color);
}

.home-screen__form__second-row i {
  margin-left: 10px;
}

.home-screen__form__imgPrev-contents {
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-items: center;
  margin-bottom: 20px;
}

.home-screen__form__imgPrev-contents__img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background-color: black;
  margin-bottom: 10px;
}

.home-screen__form__imgPrev-contents__clear {
  color: var(--twitter-color);
}

.home-screen__form__imgPrev-contents__clear #clear {
  margin-left: 5px;
}
