.login-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.login-page .fa-twitter {
  color: var(--twitter-color);
  font-size: 70px;
  display: flex;
}

.login-page__form {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}

.login-page__form input {
  margin: 5px;
  font-size: 25px;
  border: none;
  border-radius: 20px;
}

.login-page__form input::placeholder {
  padding-left: 10px;
}

.login-page__form__submit {
  background-color: var(--twitter-color);
  font-size: 10px;
  color: white;
}

.login-page__form__submit:active {
  background-color: #8ed0f9;
}

.login-page__form__signin {
  display: flex;
  justify-content: center;
  color: #8ed0f9;
  text-decoration: underline;
  margin-top: 20px;
  margin-bottom: 25px;
  cursor: pointer;
}

.social-login__button {
  border: none;
  border-radius: 20px;
  margin: 10px;
  padding: 5px;
  font-size: 15px;
}

.social-login__button i {
  margin-left: 3px;
}

.login-page__error-msg {
  color: tomato;
  margin-bottom: 20px;
}
